.card {
   height: 150px;
   width: auto;
    float: left;
    cursor: pointer;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    overflow: hidden;
    padding: 10px;
    text-align: center;
  }
  
  .card:hover {
      -webkit-transform: scale(1.06);
          -ms-transform: scale(1.06);
              transform: scale(1.06);
  }
  
  img {
    height: 150px;
    width: 150px;
  }


